import { Component, Element, Event, EventEmitter, h, Host, Method, Prop, Watch } from '@stencil/core';
import { FnIcon } from '../../utils/functional/functional';
import { generateUid } from '../../utils/utils';
import type { StormNavInterface } from './interface';

@Component({
  tag: 'storm-navs',
  styleUrl: 'storm-navs.css',
})
export class StormNavs {
  private _resizeObserver: ResizeObserver;
  private _navPillsRef: HTMLElement;
  private _navsRef: Record<string, Element>;
  private _movingDiv: HTMLElement;

  @Element() host: HTMLElement;

  @Prop({ mutable: true }) value?: string;
  @Prop() dataSet: StormNavInterface[];
  @Prop() direction: 'horizontal' | 'vertical' = 'horizontal';

  @Event({ bubbles: true }) activeChanged: EventEmitter<string>;

  @Watch('dataSet')
  async watchDataSetHandler() {
    this.initDataSet();
  }

  @Watch('value')
  async valueHandler() {
    this.moveNav();
  }

  initDataSet() {
    if (!this.dataSet || this.dataSet.length === 0) {
      return;
    }

    this.dataSet.forEach(x => {
      if (!x.id) {
        x.id = generateUid('storm-nav');
      }
    });

    if (this.value) {
      this.dataSet.forEach(x => {
        x.active = x.id === this.value;
      });
    } else if (!this.dataSet.some(x => x.active)) {
      this.dataSet[0].active = true;
      this.value = this.dataSet[0].id;
    }
  }

  @Method()
  async setActive(id: string) {
    this.value = id;
  }

  handleItemClick(ev: MouseEvent, id: string) {
    if (ev) {
      ev.preventDefault();
      ev.stopPropagation();
    }

    this.value = id;
  }

  checkSize() {}

  componentDidLoad() {
    if (!this.dataSet) return;

    const checkSize = () => {
      const activeLi = this._navPillsRef.querySelector('.nav-link.active') as HTMLElement;
      if (!activeLi.offsetWidth && !activeLi.offsetHeight) {
        return requestAnimationFrame(checkSize);
      }

      this.actionRender();
      this.bindMouseOver();
      this.observeResize();
    };

    checkSize();
    this.moveNav();
  }

  bindMouseOver() {
    this._navPillsRef.addEventListener('mouseover', (ev: MouseEvent) => {
      const target = ev.target as HTMLElement;
      const li = target.closest('li');
      if (li) {
        const nodes = Array.from(li.closest('ul')?.children || []);
        const index = nodes.indexOf(li) + 1;
        this.bindClickEvent(li, nodes, index);
      }
    });
  }

  bindClickEvent(li: HTMLElement, nodes: Element[], index: number) {
    const navLink = this._navPillsRef.querySelector(`li:nth-child(${index}) .nav-link`);
    navLink?.addEventListener(
      'click',
      () => {
        this.updateNavPosition(li, nodes, index);
      },
      { once: true },
    );
  }

  moveNav() {
    if (!this.value) return;
    this.dataSet.forEach(x => {
      x.active = x.id === this.value;
      const navElem = this._navsRef[x.id];

      navElem.classList.toggle('active', x.active);
    });

    this.updateNavPosition(
      this._navsRef[this.value].parentElement as HTMLElement,
      Array.from(this._navsRef[this.value].closest('ul').children),
      Array.from(this._navsRef[this.value].closest('ul').children).indexOf(this._navsRef[this.value].parentElement) + 1,
    );

    this.activeChanged.emit(this.value);
  }

  updateNavPosition(li: HTMLElement, nodes: Element[], index: number) {
    if (!this._movingDiv) {
      return;
    }
    let sum = 0;

    if (this._navPillsRef.classList.contains('flex-column')) {
      for (let j = 1; j <= nodes.indexOf(li); j++) {
        sum += (this._navPillsRef.querySelector(`li:nth-child(${j})`) as HTMLElement).offsetHeight;
      }
      this._movingDiv.style.transform = `translate3d(0px, ${sum}px, 0px)`;
      this._movingDiv.style.height = `${(this._navPillsRef.querySelector(`li:nth-child(${index})`) as HTMLElement).offsetHeight}px`;
    } else {
      for (let j = 1; j <= nodes.indexOf(li); j++) {
        sum += (this._navPillsRef.querySelector(`li:nth-child(${j})`) as HTMLElement).offsetWidth;
      }
      this._movingDiv.style.transform = `translate3d(${sum}px, 0px, 0px)`;
      this._movingDiv.style.width = `${(this._navPillsRef.querySelector(`li:nth-child(${index})`) as HTMLElement).offsetWidth}px`;
    }
  }

  observeResize() {
    this._resizeObserver = new ResizeObserver(() => {
      this.actionRender();
    });
    this._resizeObserver.observe(this.host);
  }

  actionRender() {
    if (this._navPillsRef == null) return;

    const activeLink = this._navPillsRef.querySelector('.nav-link.active') as HTMLElement;
    if (activeLink == null) return;

    const li = activeLink.parentElement;
    const nodes = Array.from(li.closest('ul').children);
    this.createOrUpdateMovingNav(activeLink);

    if (li) {
      const isVertical = this._navPillsRef.classList.contains('flex-column');
      const index = nodes.indexOf(li) + 1;
      const sum = this.calculateNavOffset(index, isVertical);

      this.updateMovingNav(sum, isVertical, index);
    }

    this.adjustLayoutForScreenSize();
  }

  createOrUpdateMovingNav(activeLink: HTMLElement): HTMLElement {
    if (!this._movingDiv) {
      this._movingDiv = document.createElement('div');
      const nav = activeLink.cloneNode() as HTMLElement;
      nav.textContent = '-';

      this._movingDiv.classList.add('moving-tab', 'position-absolute', 'nav-link');
      this._movingDiv.appendChild(nav);
      this._navPillsRef.appendChild(this._movingDiv);
    }

    this._movingDiv.style.padding = '0px';
    this._movingDiv.style.transition = '.5s ease';
    return this._movingDiv;
  }

  calculateNavOffset(index: number, isVertical: boolean): number {
    let sum = 0;
    for (let j = 1; j < index; j++) {
      const dimension = isVertical ? 'offsetHeight' : 'offsetWidth';
      sum += (this._navPillsRef.querySelector(`li:nth-child(${j})`) as HTMLElement)[dimension];
    }
    return sum;
  }

  updateMovingNav(sum: number, isVertical: boolean, index: number) {
    this._movingDiv.style.transform = isVertical ? `translate3d(0px, ${sum}px, 0px)` : `translate3d(${sum}px, 0px, 0px)`;
    this._movingDiv.style[`width`] = (this._navPillsRef.querySelector(`li:nth-child(${index})`) as HTMLElement).offsetWidth + 'px';
    this._movingDiv.style[`height`] = (this._navPillsRef.querySelector(`li:nth-child(${index})`) as HTMLElement).offsetHeight + 'px';
  }

  adjustLayoutForScreenSize() {
    if (window.innerWidth < 991 && !this._navPillsRef.classList.contains('flex-column')) {
      this.toggleLayout(true);
    } else if (window.innerWidth >= 991 && this._navPillsRef.classList.contains('on-resize')) {
      this.toggleLayout(false);
    }
  }

  toggleLayout(isVertical: boolean) {
    if (isVertical) {
      this._navPillsRef.classList.remove('flex-row');
      this._navPillsRef.classList.add('flex-column', 'on-resize');
    } else {
      this._navPillsRef.classList.remove('flex-column', 'on-resize');
      this._navPillsRef.classList.add('flex-row');
    }
    const li = this._navPillsRef.querySelector('.nav-link.active').parentElement;
    const nodes = Array.from(li.closest('ul').children);
    const sum = this.calculateNavOffset(nodes.indexOf(li) + 1, isVertical);
    this._movingDiv.style.transform = isVertical ? `translate3d(0px, ${sum}px, 0px)` : `translate3d(${sum}px, 0px, 0px)`;
  }

  componentWillRender() {
    this.initDataSet();
  }

  render() {
    this._navsRef = {};
    if (!this.dataSet) return;
    return (
      <Host>
        <div class="nav-wrapper position-relative end-0 m-1">
          <ul
            ref={elm => (this._navPillsRef = elm)}
            class={{
              nav: true,
              'nav-pills': true,
              'nav-fill': true,
              'p-1': true,
              'flex-column': this.direction === 'vertical',
            }}
          >
            {this.dataSet.map(x => (
              <li class="nav-item">
                <a
                  ref={elm => (this._navsRef[x.id] = elm)}
                  class={{
                    'nav-link': true,
                    'mb-0': true,
                    'px-0': true,
                    'py-1': true,
                    active: x.active,
                    disabled: x.disabled,
                  }}
                  onClick={ev => this.handleItemClick(ev, x.id)}
                  href="#"
                >
                  <FnIcon>{x.icon}</FnIcon>
                  {x.name}
                </a>
              </li>
            ))}
          </ul>
        </div>
      </Host>
    );
  }
}
